.portfolio__card {
  &__subheading {
    color: $text;
    font-size: 16px;
    font-family: $LB;
    line-height: 22px;
    font-weight: 600;
    text-transform: uppercase;
    padding-right: 20px;
    cursor: pointer;
    &:hover {
      color: $accent;
      transition: 0.5s;
    }
  }
}
